import { useState } from "react";
import styled, { css } from "styled-components";

const skins_list = require("./lib/skin_list.json");
const chars_list = require("./lib/sprite_list.json");
const images_list = require("./lib/images.json");

const Background = styled.div`
  background: #484848;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;

const CatBackground = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  overflow: hidden;
`;

const CatSearch = styled.div`
  margin-bottom: 1rem;
  background: #212121;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const SearchBox = styled.input`
  width: 100%;
`;

const CatBody = styled.div`
  height: 100%;
  padding: 1rem;
  background: #212121;
  overflow: hidden;
`;

const Nav = styled.div`
  background: #212121;
  width: 100%;
`;

const NavContainer = styled.div`
  padding: 1rem;
  display: flex;
  justify-content: space-around;
`;

const NavButton = styled.div`
  color: white;
  user-select: none;
  cursor: pointer;
  padding: 0.25rem;

  ${(props) =>
    props.primary &&
    css`
      background: white;
      color: black;
    `}
`;

const BackgroundImage = styled.img`
  width: auto;
  height: auto;
  max-width: 256px;
  max-height: 144px;
  margin: 0.25rem;
`;

const CatContent = styled.div`
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow: auto;
`;

const CharCard = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items:center;
  margin: 0.25rem;
  background: #484848;
  color: white;
  padding: 0.25rem;
  cursor: pointer;
`;

const CharImg = styled.img`
  width: auto;
  height: auto;
  max-height: 240px;
  max-width: 240px;
`;

const BackButton = styled.div`
  padding: 0.25rem;
  margin-top: 1rem;
  background: white;
  color: black;
  width: 50%;
  text-align: center;
  cursor:pointer;
`;

function Operators() {
  const [search, setSearch] = useState("");
  const [op, setOP] = useState("");
  const skinName = (skin) => {
    switch (skin.skinGroupId) {
      case `ILLUST_0`:
        return `Elite 0`;
      case `ILLUST_2`:
        return `Elite 2`;
      default:
        return `${skin.skinName} (${skin.skinGroupId} : ${skin.skinGroupName})`;
    }
  };
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
        {op ? <BackButton onClick={() => setOP("")}>Back</BackButton> : <></>}
      </CatSearch>
      <CatBody>
        <CatContent>
          {op
            ? skins_list[op]["skins"].map((s) => (
                <a
                  href={`./images/skins/${s.skinId}.png`}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={s.skinId}
                >
                  <CharCard>
                    <CharImg src={`./images/skins/${s.skinId}.png`}></CharImg>
                    <div>{skinName(s)}</div>
                  </CharCard>
                </a>
              ))
            : search
            ? Object.keys(skins_list)
                .filter(
                  (c) =>
                    skins_list[c]["charId"].toLowerCase().search(search.toLowerCase()) >= 0 ||
                    skins_list[c]["charName"].toLowerCase().search(search.toLowerCase()) >= 0
                )
                .map((c) => (
                  <CharCard key={c} onClick={() => setOP(c)}>
                    <CharImg
                      src={`./images/skins/${skins_list[c]["skins"][0]["skinId"]}.png`}
                    ></CharImg>
                    <div>{`${skins_list[c]["charId"]} (${skins_list[c]["charName"]})`}</div>
                  </CharCard>
                ))
            : Object.keys(skins_list).map((c) => (
                <CharCard key={c} onClick={() => setOP(c)}>
                  <CharImg
                    src={`./images/skins/${skins_list[c]["skins"][0]["skinId"]}.png`}
                  ></CharImg>
                  <div>{`${skins_list[c]["charId"]} (${skins_list[c]["charName"]})`}</div>
                </CharCard>
              ))}
        </CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Characters() {
  const [search, setSearch] = useState("");
  const [char, setChar] = useState("");
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
        {char ? (
          <BackButton onClick={() => setChar("")}>Back</BackButton>
        ) : (
          <></>
        )}
      </CatSearch>
      <CatBody>
        <CatContent>
          {char
            ? Object.keys(chars_list[char]).map((s) => (
                <a
                  href={`./images/characters/${chars_list[char][s]["sprite"]}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={`${char}_${s}`}
                >
                  <CharCard>
                    <CharImg
                      src={`./images/characters/${chars_list[char][s]["sprite"]}`}
                    ></CharImg>
                    <div>{`${s} ${chars_list[char][s]["alias"]}`}</div>
                  </CharCard>
                </a>
              ))
            : search
            ? Object.keys(chars_list)
                .filter((s) => s.search(search) >= 0)
                .map((c) => (
                  <CharCard onClick={() => setChar(c)} key={c}>
                    <CharImg
                      src={`./images/characters/${chars_list[c]["1"]["sprite"]}`}
                    ></CharImg>
                    <div>{c}</div>
                  </CharCard>
                ))
            : Object.keys(chars_list).map((c) => (
                <CharCard onClick={() => setChar(c)} key={c}>
                  <CharImg
                    src={`./images/characters/${chars_list[c]["1"]["sprite"]}`}
                  ></CharImg>
                  <div>{c}</div>
                </CharCard>
              ))}
        </CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Backgrounds(props) {
  const [search, setSearch] = useState("");
  const images =
    props.type === "backgrounds" ? images_list.backgrounds : images_list.images;
  return (
    <CatBackground>
      <CatSearch>
        <SearchBox
          type={`text`}
          onInput={(e) => setSearch(e.target.value)}
        ></SearchBox>
      </CatSearch>
      <CatBody>
        <CatContent>
          {search
            ? images
                .filter((i) => i.search(search) >= 0)
                .map((bg) => (
                  <a
                    href={`./images/${props.type}/${bg}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    key={bg}
                  >
                    <BackgroundImage
                      src={`./images/${props.type}/${bg}`}
                      alt={bg}
                    />
                  </a>
                ))
            : images.map((bg) => (
                <a
                  href={`./images/${props.type}/${bg}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={bg}
                >
                  <BackgroundImage
                    src={`./images/${props.type}/${bg}`}
                    alt={bg}
                  />
                </a>
              ))}
        </CatContent>
      </CatBody>
    </CatBackground>
  );
}

function Images() {
  const [cat, setCat] = useState("");
  return (
    <Background>
      <Nav>
        <NavContainer>
          <NavButton
            primary={cat === "operators"}
            onClick={() => setCat("operators")}
          >
            Operators
          </NavButton>
          <NavButton
            primary={cat === "characters"}
            onClick={() => setCat("characters")}
          >
            Characters
          </NavButton>
          <NavButton
            primary={cat === "backgrounds"}
            onClick={() => setCat("backgrounds")}
          >
            Backgrounds
          </NavButton>
          <NavButton
            primary={cat === "images"}
            onClick={() => setCat("images")}
          >
            Images
          </NavButton>
        </NavContainer>
      </Nav>
      {cat === `backgrounds` ? <Backgrounds type="backgrounds" /> : <></>}
      {cat === `images` ? <Backgrounds type="images" /> : <></>}
      {cat === `characters` ? <Characters /> : <></>}
      {cat === `operators` ? <Operators /> : <></>}
    </Background>
  );
}

export default Images;
